import slug from "mollusc"
import moment from "moment"

moment.locale("de")
slug.charmap["."] = "-"

function propsToArray(obj, prefix) {
  /* wraps a series of flat props into an array like so
  props = {
    post1: "a",
    post2: "b",
    post3: "c"
  }
  array = propsToArray(props, "post")
  => ["a", "b", "c"]
  */
  const array = []
  let i = 0
  let key = `${prefix}${i}`
  while (obj.hasOwnProperty(key)) {
    array.push(obj[key])
    i += 1
    key = `${prefix}${i}`
  }
  return array
}

function readingTimeText(wordcount) {
  let minutes = Math.round(wordcount / 200)
  if (minutes == 0) {
    minutes = 1
  }
  return minutes == 1 ? "1 Minute Lesedauer" : `${minutes} Minuten Lesedauer`
}

function arrayToObj(array, prefix) {
  const data = {}
  array.forEach((item, i) => data[`${prefix}${i}`] = item)
  return data
}

function pageSuffix(page) {
  return page == 0 ? "" : `/${page + 1}`
}

function toDate(string) {
  const timestamp = Date.parse(string)
  if (isNaN(timestamp)) {
    throw new Error(`"${string}" is not a valid date`)
  }
  return new Date(timestamp)
}

function pad(number, digits) {
  number = new String(number)
  while (number.length < digits) {
    number = "0" + number
  }
  return number
}

function makeSlug(revdate, doctitle) {
  const date = toDate(revdate)
  const titleSlug = slug(doctitle)
  const dateSlug = [
    date.getFullYear(),
    pad(date.getMonth() + 1, 2),
    pad(date.getDate(), 2)
  ].join("/")
  return `${dateSlug}/${titleSlug}`
}

function formatDate(date) {
  date = moment(date)
  const daysAgo = moment().diff(date, "days")
  if (daysAgo > 7) {
    return date.format("Do MMMM YYYY")
  } else if (daysAgo >= 2) {
    return date.fromNow()
  } else {
    return date.calendar().split(" ")[0]
  }
}

export {
  arrayToObj,
  propsToArray,
  readingTimeText,
  pageSuffix,
  toDate,
  pad,
  makeSlug,
  formatDate
}


