import React from "react"
import { Icon } from "rmwc/Icon"
import { Link } from "react-static"

const showMaxTags = 10

const BlogTags = ({ tags, currentTag }) => (
  <section id="blog-tags" className="info-box">
    <header className="info-box__header">
      <h3 className="info-box__title">
        <Icon>local_offer</Icon>
        Tags
      </h3>
    </header>
    <div className="info-box__body">
      <ul style={{ 
        listStyleType: "none",
        margin: "0",
        padding: "0" 
      }}>
        {
          tags.slice(0, showMaxTags).map(({ tag, posts }) => (
            <li key={tag}>
              <Link 
                to={`/blog/tag/${tag}`} 
                title={`Zeige alle Posts mit Tag "${tag}"`}
                style={{
                  fontWeight: tag === currentTag ? "400" : "300"
                }}
              >
                {`${tag} (${posts})`}
              </Link>
            </li>
          ))
        }
      </ul>
    </div>
  </section>
)

export default BlogTags


