import React from "react"
import { Icon } from "rmwc/Icon"
import { Link } from "react-static"

const showMaxAuthors = 5

const BlogAuthors = ({ authors, currentAuthor }) => (
  <section id="blog-authors" className="info-box">
    <header className="info-box__header">
      <h3 className="info-box__title">
        <Icon>person</Icon>
        Autoren
      </h3>
    </header>
    <div className="info-box__body">
      <ul style={{ 
        listStyleType: "none",
        margin: "0",
        padding: "0" 
      }}>
        {
          authors.slice(0, showMaxAuthors).map(({ author, posts }) => (
            <li key={author}>
              <Link 
                to={`/blog/author/${author}`} 
                title={`Zeige alle Posts mit Autor "${author}"`}
                style={{
                  fontWeight: author === currentAuthor ? "400" : "300"
                }}
              >
                {`${author} (${posts})`}
              </Link>
            </li>
          ))
        }
      </ul>
    </div>
  </section>
)

export default BlogAuthors


