import React from "react"
import i18n from "linergy-de/i18n"
import { withRouteData } from "react-static"

export default withRouteData(
  class LanguageDetector extends React.Component {
    constructor(props) {
      super(props)
      const lng = props.lng || "de"
      this.changeLanguage(lng)
    }

    render() {
      return null
    }

    changeLanguage(lng) {
      i18n.prevLanguage = i18n.language
      i18n.changeLanguage(lng)
    }

    componentWillReceiveProps({ lng }) {
      if (lng === this.props.lng) {
        return 
      }
      lng = lng || "de"
      this.changeLanguage(lng)
    }
  }
)


