import React from "react"
import Button from "rmwc/Button"
import { Grid, GridCell } from "rmwc/Grid"
import { Link } from "react-static"

export default class CookieAccept extends React.Component {
  state = {
    cookiesAccepted: true,
  }

  constructor () {
    super()
    if (typeof localStorage !== "undefined") {
      this.state.cookiesAccepted = (
        localStorage.getItem("linergy-cookies-accepted-dsgvo") === "true"
      )
    }
  }

  render () {
    if (this.state.cookiesAccepted) { 
      return null 
    }
    return (
      <Grid
        id="linergy-cookie-accept"
        style={{
          position: "fixed",
          boxSizing: "border-box",
          bottom: "0",
          left: "50%",
          right: 0,
          flexDirection: "row",
          width: "100%",
          maxWidth: "1050px",
          backgroundColor: "#323232",
          color: "white",
          border: "1px solid #333",
          lineHeight: "1.4",
          borderRadius: "2px",
          transform: "translate(-50%)",
          display: "flex",
        }}
      >
        <GridCell desktop="10" tablet="8">
          linergy.de benutzt Cookies, um seinen Besuchern das beste Webseiten-Erlebnis
          zu ermöglichen.
          Des Weiteren werden teilweise auch Cookies von Diensten Dritter verwendet.
          Weitere Informationen dazu erhalten Sie in
          der <Link to="/datenschutz" style={{ color: "white" }}><strong>Datenschutzerklärung</strong></Link> von linergy.de.
        </GridCell>
        <GridCell
          desktop="2"
          tablet="8"
          style={{
            whiteSpace: "nowrap",
            display: "flex",
            alignItems: "center",
          }}
        >
          <Button
            raised
            onClick={() => {
              localStorage.setItem("linergy-cookies-accepted-dsgvo", "true")
              this.setState({ cookiesAccepted: true })
            }}
          >
            Bestätigen
          </Button>
        </GridCell>
      </Grid>
    )
  }
}



