import React from "react"
import { CardMedia } from "rmwc/Card"
import Icon from "rmwc/Icon"

const ProductCardMedia = ({ icon, image, mediaStyle, ...rest }) => (
  <CardMedia
    style={{
      ...mediaStyle,
      height: "12rem",
      textAlign: "center",
      padding: "1rem 1rem 0 1rem",
      display: "flex",
      alignItems: "center",
      justifyContent: "center"
    }}
    {...rest}
  >
    {icon ? (
      <Icon style={{ fontSize: "140px" }}>{icon}</Icon>
    ) : (
      <div
        style={{
          height: "100%",
          width: "100%",
          background: `url(${image})`,
          backgroundSize: "contain",
          backgroundPosition: "50% 0",
          backgroundRepeat: "no-repeat"
        }}
      />
    )}
  </CardMedia>
)

export default ProductCardMedia



