import React from "react"
import { CircleIcon } from "linergy-de/components"

import FacebookIconBase from "mdi-react/FacebookIcon"
import TwitterIconBase from "mdi-react/TwitterIcon"
import GooglePlusIconBase from "mdi-react/GooglePlusIcon"
import EmailIconBase from "mdi-react/EmailIcon"
import LinkIconBase from "mdi-react/LinkVariantIcon"
import YoutubePlayIconBase from "mdi-react/YoutubePlayIcon"

export const FacebookIcon = ({ innerProps = {}, ...props }) => (
  <CircleIcon color="#265292" {...props}>
    <FacebookIconBase {...innerProps} />
  </CircleIcon>
)

export const TwitterIcon = ({ innerProps = {}, ...props }) => (
  <CircleIcon color="#03b3ee" {...props}>
    <TwitterIconBase {...innerProps} />
  </CircleIcon>
)

export const GooglePlusIcon = ({ innerProps = {}, ...props }) => (
  <CircleIcon color="#e15440" {...props}>
    <GooglePlusIconBase {...innerProps} />
  </CircleIcon>
)

export const EmailIcon = ({ innerProps = {}, ...props }) => (
  <CircleIcon color="#9e968e" {...props}>
    <EmailIconBase {...innerProps} />
  </CircleIcon>
)

export const LinkIcon = ({ innerProps = {}, ...props }) => (
  <CircleIcon color="#ffb527" {...props}>
    <LinkIconBase {...innerProps} />
  </CircleIcon>
)

export const YoutubePlayIcon = ({ innerProps = {}, ...props }) => (
  <CircleIcon color="#ff0000" {...props}>
    <YoutubePlayIconBase {...innerProps} />
  </CircleIcon>
)


