import i18n from "i18next"
import { translate } from "react-i18next"

i18n.init({
  load: "languageOnly",
  fallbackLng: "de",
  debug: false,
  lng: "de",
  defaultNS: "translations",
  interpolation: {
    escapeValue: false
  },
  react: {
    wait: true
  }
})

translate.setI18n(i18n)

export default i18n


