import React from "react"
import TextField, { TextFieldHelperText } from "rmwc/TextField"
import { withFormMailer } from "linergy-de/components"
import i18n from "linergy-de/i18n"

const t = i18n.getFixedT(null, "contact")

function validateEmail(email) {
  const re = /\S+@\S+\.\S+/
  return re.test(email)
}

const ContactForm = withFormMailer({ 
  to: "info@linergy.de", 
  data: { 
    subject: "Kontaktanfrage von linergy.de",
    email: "",
    message: "",
    name: "",
    ackDataProtection: false
  },
  validate: function(data) {
    const errors = {}
    let error = false
    if (!validateEmail(data.email)) {
      errors.email = t("addressInvalid", "E-Mail Adresse ist ungültig")
      error = true
    }
    if (data.message.length <= 10) {
      errors.message = t("messageTooShort", "Sie müssen mindestens zehn Zeichen eingeben")
      error = true
    }
    return error ? errors : null
  }
})(({ handleChange, data, errors }) => (
  <div>
    <TextField
      name="name" 
      label={t("yourName", "Ihr Name")}
      className="fill-width"
      box
      onChange={handleChange}
      value={data.name}
    />
    <TextFieldHelperText persistent validationMsg>
      {errors.name}
    </TextFieldHelperText>
    <TextField 
      name="email"
      label={t("yourEmail", "Ihre E-Mail Adresse")} 
      box
      className="fill-width"
      onChange={handleChange}
      value={data.email}
    />
    <TextFieldHelperText persistent>
      {t("isRequired", "Dieses Feld muss ausgefüllt werden")}
    </TextFieldHelperText>
    <TextFieldHelperText persistent validationMsg>
      {errors.email}
    </TextFieldHelperText>
    <TextField 
      name="message" 
      textarea
      fullwidth
      box
      placeholder={t("yourMessage", "Ihre Nachricht")}
      rows="8"
      onChange={handleChange}
      value={data.message}
    />
    <TextFieldHelperText persistent>
      {t("isRequired", "Dieses Feld muss ausgefüllt werden")}
    </TextFieldHelperText>
    <TextFieldHelperText persistent validationMsg>
      {errors.message}
    </TextFieldHelperText>
  </div>
))

export default ContactForm


