import React from "react"
import { Row, GridCell, translate, Trans } from "linergy-de/components"
import { Link } from "react-static"
import Button from "rmwc/Button"

// images
import buildingImg from "./img/building.jpg"
import "./about.css"
import SEO from "../../components/SEO"


const About = ({ t }) => (
  <div id="about">
    <SEO image={buildingImg} />
    <Row id="building-row" grid style={{
      background: `url(${buildingImg}) no-repeat`,
      backgroundSize: "cover"
    }}>
      <GridCell tablet="6" desktop="8" className="white-box">
        <Trans i18nKey="intro">
          <h1>Kompetenter Service</h1>
          <p>
            Wir bieten Ihnen umfassende Service- und Beratungsleistungen zu den 
            Themenbereichen Lithiumakkumulatoren und Antriebstechnik. 
          </p>
          <p>
            Alle gängigen Prüf- und Entwicklungsarbeiten können wir Ihnen zeitnah 
            und aus eigener Hand anbieten.  
          </p>
          <Link to={t("contactUsLink", "/kontakt")}>
            <Button raised>Kontaktieren Sie uns</Button>
          </Link>
        </Trans>
      </GridCell>
    </Row>
    <Row grid>
      <GridCell span="12">
        <Trans i18nKey="body">
          <p>
            Linergy entwickelt und produziert seit 2012 Batteriesysteme für 
            High-Tech-Anwedungen in den Bereichen Elektromobilität, Speicherlösungen, 
            Medizin und Robotik.
          </p>
          <p>
            Als verlässlicher und kundenorientierter Partner für viele 
            Technologieunternehmen sind wir ein kompetenter Ansprechpartner für 
            alle Themen rund um die Planung, Projektierung und Entwicklung von 
            modernsten Produkten für Industrie und Privatanwender. 
          </p>
          <p>
            Unsere Ingenieure haben stets die vielfältigen Anforderungen unserer 
            Kunden im Blick, sei es bei der Konfektionierung von Standardprodukten 
            oder auch bei der Begleitung vom ersten Prototypen bis hin zur 
            Serienfertigung. 
          </p>
          <p>
            Als Vorreiter der Automatisierungstechnik im Bereich der Akkufertigung 
            können wir einen umfassenden Service bei der Realisierung Ihrer 
            Projekte bieten. Ob Prototypenfertigung oder On-Demand Serienfertigung, 
            durch unsere flexiblen Arbeitsabläufe erhalten Sie ein attraktives 
            und preiswertes Gesamtpaket. 
          </p>
        </Trans>
      </GridCell>
    </Row>
  </div>
)

export default translate("about")(About)


