import React from "react"
import { Row, GridCell, translate, Trans, SEO } from "linergy-de/components"

import Icon from "rmwc/Icon"
import Button from "rmwc/Button"

// images
import batteryRenderingImg from "./img/battery-rendering.png"
import bmsShadowImg from "./img/bms-shadow.png"
import voltageOverSOCImg from "./img/voltage-over-soc.png"

const BMS = translate("bms")(() => (
  <div id="bms">
    <SEO image={bmsShadowImg} />
    <Row className="bg--blueish-striped" style={{ minHeight: "500px" }} grid>
      <GridCell desktop="6" tablet="8" style={{ paddingBottom: "12px" }}>
        <Trans i18nKey="intro">
          <h1>Besser mit System</h1>
          <p>
            Batterie-Management-Systeme (BMS) schützen den Akku vor Schäden durch 
            Fehlbedienung und sorgen für eine gleichmäßige Spannungsverteilung. 
          </p>
          <p>
            Als zentrale Steuereinheit eines modernen Akkus beinhaltet es 
            wesentliche Schutzmechanismen, die für sicheren und zuverlässigen 
            Betrieb unerlässlich sind. Moderne Schnittstellen (CANBus, RS485) 
            bieten darüber hinaus die Möglichkeit der Überwachung und Integration 
            in komplexe Anwendungen. 
          </p>
          <Button raised tag="a" href="https://www.linergy-shop.de/de/BMS-Schutzelektronik//">BMS im Onlineshop ansehen</Button>
        </Trans>
      </GridCell>
      <GridCell desktop="6" tablet="7">
        <img src={bmsShadowImg} style={{ maxWidth: "120%" }} alt="BMS" />
      </GridCell>
    </Row>
    <Row grid>
      <GridCell>
        <Trans i18nKey="protection">
          <h3 className="icon-header"><Icon>security</Icon>Schutz</h3>
          <p className="justify">Unsere BMS ermöglichen vollintegrierten Schutz 
          für Li-Ion Akkupacks. Tiefentladung und Überladung werden durch eine 
          Spannungsüberwachung der einzelnen Zellen wirkungsvoll verhindert und 
          so die Langlebigkeit des Akkus sichergestellt. </p>
        </Trans>
      </GridCell>

      <GridCell>
        <Trans i18nKey="balancing">
          <h3 className="icon-header"><Icon>show_chart</Icon>Balancing</h3>
          <p className="justify">
            Die Balancing Funktion wirkt beim Ladevorgang dem Drift seriell 
            verschalteter Zellen entgegen und sorgt somit für eine gleichmäßige 
            Spannungsverteilen im Akku. Dadurch werden die Zellen gleichmäßiger 
            belastet und die Energieausbeute maximiert. 
          </p>
        </Trans>
      </GridCell>

      <GridCell tablet="8">
        <Trans i18nKey="operationalSafety">
          <h3 className="icon-header"><Icon>thumb_up</Icon>Betriebssicherheit</h3>
          <p className="justify">
            Mit einem BMS erhöhen Sie die Betriebssicherheit eines Akkus enorm. 
            Thermische Überbeanspruchung durch Betrieb mit zu hohen Entladeströmen 
            oder Überlastung durch Kurzschlüsse in der Außenbeschaltung können 
            frühzeitig erkannt werden und der Akku durch eine Sicherheitsschaltung 
            abgeschaltet werden.
          </p>
        </Trans>
      </GridCell>
    </Row>
    <Row grid className="border-top">
      <Trans i18nKey="operatingWindow">
        <GridCell span="12">
          <h1>Betriebsfenster von Li-Ion Zellen</h1>
        </GridCell>
        <GridCell span="8">
          Li-Ion Zellen reagieren sehr empfindlich auf die Unterschreitung einer 
          Mindestspannung beim Entladen oder auch die Überschreitung der 
          Ladeendspannung beim Wiederaufladen. Dies kann im schlimmsten Fall 
          zur irreparablen Beschädigung der Zelle und damit zum Defekt des 
          ganzen Akkupacks führen. Dabei besteht auch die Gefahr der Entzündung 
          bei starker Überhitzung. Unsere BMS unterbrechen im Notfall sämtlichen 
          Stromfluss um sämtliche Akkuzellen im erlaubten Spannungsbereich 
          zu halten. 
        </GridCell>
      </Trans>
      <GridCell tablet="8">
        <img src={voltageOverSOCImg} style={{maxWidth: "100%"}} alt="V/SOC" />
      </GridCell>
    </Row>

    <Row grid className="border-top">
      <Trans i18nKey="oneStopSolution">
        <GridCell span="12">
          <h1>Alles aus einer Hand</h1>
        </GridCell>
        <GridCell span="8">
          Für sämtliche unserer Akkuangebote und Spezialanfertigungen bieten 
          wir selbstverständlich direkt ab Werk die passende BMS Elektronik an. 
          Eine präzise Abstimmung auf die verbauten Zellen gewährleistet 
          optimalen Betrieb und bestmögliche Leistung.  
          <p>
            Dank des fachgerechten Einbau durch unsere qualifizierten Mitarbeiter 
            erhalten Sie ein Produkt von hoher Qualität an dem Sie lange Freude 
            haben. Natürlich können wir dabei auch auf Ihre speziellen 
            Anforderungen eingehen. Kontaktieren Sie uns einfach, wir finden für 
            jede Anwendung das passende BMS! 
          </p>
        </GridCell>
      </Trans>
      <GridCell tablet="8">
        <img src={batteryRenderingImg} style={{maxWidth: "100%"}} alt="Battery" />
      </GridCell>
    </Row>
  </div>
))

export default BMS


