import React from "react"
import TextField from "rmwc/TextField"
import { withRouteData } from "react-static"

class SearchField extends React.Component {
  state = {
    error: null,
    searchString: ""
  }

  constructor() {
    super()
    this.handleChange = this.handleChange.bind(this)
    this.handleKeyPressed = this.handleKeyPressed.bind(this)
  }

  handleChange({ target: { value } }) {
    this.setState({ searchString: value })
  }

  handleKeyPressed(e) {
    if (e.keyCode !== 13)  {
      return
    }
    this.search()
  }

  search() {
    const { history, lng, onSearch } = this.props
    const route = lng == "de" ? "/suche" : "/en/search"
    history.push(route, { searchString: this.state.searchString })
    onSearch && onSearch()
  }

  focus() {
    this.input && this.input.focus()
  }

  componentDidMount() {
    const { searchRef } = this.props
    if (searchRef) {
      searchRef(this)
    }
  }

  render() {
    const { id, style, className, label, box, fullwidth, rootProps } = this.props
    return (
      <TextField
        {...{ id, style, className, label, box, fullwidth, rootProps }}
        withTrailingIcon="search"
        onKeyDown={this.handleKeyPressed}
        onChange={this.handleChange}
        value={this.state.searchString}
        inputRef={r => this.input = r}
      />
    )
  }
}

export default withRouteData(SearchField)


