import React from "react"
import { NavLink, withRouter, matchPath } from "react-static"

const DrawerMenuSecondary = ({ menu, parentPath, location, handleClick }) => {
  if (!menu) {
    return null 
  }
  const match = matchPath(location.pathname, { path: `${parentPath}` })
  return (
    <ul className={`
      ${match ? "drawer-nav__links-secondary--active" : ""} drawer-nav__links-secondary
    `}>
      {
        menu.map(entry => (
          <li key={entry[0]}>
            <NavLink to={`${entry[0]}`} exact onClick={handleClick}>{entry[1]}</NavLink>
          </li>
        ))
      }
    </ul>
  )
}

export default withRouter(DrawerMenuSecondary)


