import React from "react"
import { withRouteData } from "react-static"
import { Row, GridCell, Adoc, NotAvailableInLng } from "linergy-de/components"

const ArticlePage = ({ article, showHeader }) => (
  <div id="article">
    <Row grid>
      <NotAvailableInLng availableIn={["de"]} />
      <GridCell span="12">
        <Adoc article={article} showHeader={showHeader} />
      </GridCell>
    </Row>
  </div>
)

export default withRouteData(ArticlePage)



