
import React from "react"
import { Icon } from "rmwc/Icon"
import { SearchField } from "linergy-de/components"
import { Button } from "rmwc/Button"

class BlogSearch extends React.Component {
  render() {
    return (
      <section id="blog-search" className="info-box">
        <header className="info-box__header">
          <h3 className="info-box__title">
            <Icon>search</Icon>
            Blog durchsuchen
          </h3>
        </header>
        <div className="info-box__body">
          <SearchField
            box
            className="fill-width"
            label="In Blog suchen"
            searchRef={r => this.searchField = r}
          />
          <Button 
            raised
            style={{
              width: "100%",
              marginTop: "12px"
            }}
            onClick={() => this.searchField.search()}
          >
            Suchen
          </Button>
        </div>
      </section>
    )
  }
}

export default BlogSearch


