import React from "react"
import axios from "axios"
import moment from "moment"
import Button from "rmwc/Button"
import { Link } from "react-static"

function answersText(count) {
  if (count === 0) {
    return "keine Antworten"
  } else if (count === 1) {
    return "eine Antwort"
  } else {
    return `${count} Antworten`
  }
}

const discourseUrl = "https://forum.linergy.de"

class PostComments extends React.Component {
  state = {
    postsCount: 0,
    posts: [],
    slug: "",
    show: false
  }

  render() {
    const { topicId, show } = this.props
    if (!topicId) {
      return null
    }
    if (!this.state.show && show !== true) {
      return (
        <div className="post__comments-init">
          <a 
            href={`${discourseUrl}/t/${this.state.slug}`}
            onClick={e => {
              e.preventDefault()
              this.setState({ show: true })
            }}
            
          >
            Diskussion ({answersText(this.state.postsCount)})
          </a>
        </div>
      )
    }
    return (
      <div className="post__comments">
        <header className="discourse-header">
          <Link to={`${discourseUrl}/t/${this.state.slug}`}>
            <Button raised>Kommentar schreiben</Button>
          </Link>
          <span className="discourse-header__answers">
            {answersText(this.state.postsCount)}
          </span>
        </header>
        {
          this.state.posts.map(post => (
            <article className="comment" key={post.id}>
              <div className="comment__avatar">
                <a href={`${discourseUrl}/u/${post.username}`}> 
                  <img src={`${discourseUrl}${post.avatar_template}`.replace("{size}", "36")} alt={post.username} />
                </a>
              </div>
              <div className="comment__content">
                <div className="comment__header">
                  <div className="comment__author">
                    <a href={`${discourseUrl}/u/${post.username}`}> 
                      {post.username}
                    </a>
                  </div>
                  <time className="comment__date" dateTime={post.updated_at}>
                    {moment(post.updated_at).calendar()}
                  </time>
                </div>
                <div className="comment__cooked" dangerouslySetInnerHTML={{ __html: post.cooked }} />
              </div>
            </article>
          ))
        }
      </div>
    )
  }

  async loadComments() {
    const { data } = await axios.get(`${discourseUrl}/t/${this.props.topicId}.json`)
    const { 
      posts_count: postsCount,
      post_stream: { posts },
      slug
    } = data

    this.setState({ postsCount, posts, slug })
  }

  componentDidMount() {
    if (this.props.topicId) {
      this.loadComments()
    }
  }
}

export default PostComments


