import React from "react"
import { Link, withRouteData } from "react-static"
import { Row, InnerRow } from "linergy-de/components/Row"
import logoImg from "linergy-de/img/linergy-logo.svg"
import RotationToggle from "./RotationToggle"
import Breadcrumbs from "./Breadcrumbs"
import SearchBar from "./SearchBar"
import DrawerMenu from "./DrawerMenu"
import { HorizontalMenuPrimary, HorizontalMenuSecondary } from "./HorizontalMenu"

import "./toolbar.css"

class Toolbar extends React.Component {
  state = {
    searchOpen: false
  }

  constructor() {
    super()
    this.toggleSearch = this.toggleSearch.bind(this)
    this.hideSearchBar = this.hideSearchBar.bind(this)
  }

  toggleSearch(e) {
    e.preventDefault()
    this.setState(prevState => ({ searchOpen: !prevState.searchOpen })) 
  }

  hideSearchBar() {
    this.setState({ searchOpen: false })
  }

  render() {
    const { menu, lng } = this.props
    const home = lng === "de" ? "/" : `/${lng}/`
    return (
      <Row 
        id="toolbar" 
        style={{ 
          borderBottom: "1px solid rgba(0,0,0,.15)", 
          paddingBottom: "12px",
          paddingTop: "12px"
        }} 
        className="h-nav"
      >
        <InnerRow 
          id="h-first-row"
          className="h-nav__row" 
          style={{ height: "40px" }}
        >
          <DrawerMenu />
          <Link to={home}><img id="toolbar-logo" src={logoImg} alt="linergy" /></Link>
          <HorizontalMenuPrimary />
          <RotationToggle
            id="search-toggle"
            icon="search"
            label="Search"
            active={this.state.searchOpen}
            onClick={this.toggleSearch}
          />
        </InnerRow>
        <InnerRow
          id="h-second-row"
          style={{ marginTop: "6px"}} 
          className={`
          h-nav__row
          ${menu[1] ? "" : "h-nav__row--hidden"}
        `.trim()}
        >
          <Breadcrumbs />
          <HorizontalMenuSecondary />
        </InnerRow>
        <SearchBar open={this.state.searchOpen} onSearch={this.hideSearchBar} />
      </Row>
    )
  }
}

export default withRouteData(Toolbar)


