import React from "react"
import { NavLink } from "react-static"
import "./style.css"

const HoverMenu = ({ menu }) => {
  if (!menu) {
    return null 
  }
  return (
    <ul className="hover-menu mdc-elevation--z2">
      {
        menu.map(entry =>(
          <li key={entry[0]} className="hover-menu__link">
            <NavLink to={`${entry[0]}`} exact>{entry[1]}</NavLink>
          </li>
        ))
      }
    </ul>
  )
}

export default HoverMenu


