import { translate as translateOriginal, Trans } from "react-i18next"
import React, { Component } from "react"
import { withRouteData } from "react-static"
import i18n from "linergy-de/i18n"

const translate = (ns, extraNs) => (
  Comp => {
    const TranslatedComponent = translateOriginal(ns)(Comp)
    const allNamespaces = [ns, ...(extraNs || [])]
    return withRouteData(
      class Translate extends Component {
        constructor(props) {
          super(props)
          this.loadLocales(props)
        }

        loadLocales(props) {
          const { lng } = props
          if (!lng) {
            return
          }
          const localesMap = {}
          Object.keys(props).forEach(key => {
            if (!key.startsWith("locale_")) {
              return
            }
            const ns = key.slice(7)
            localesMap[ns] = props[key]
          })
          allNamespaces.forEach(ns => {
            this.addLocale(lng, ns, localesMap[ns])
          })
        }

        addLocale(lng, ns, locale) {
          if (!i18n.hasResourceBundle(lng, ns)) {
            locale = locale || {} // Empty resource bundle for default language
            i18n.addResourceBundle(lng, ns, locale)
          }
        }

        componentWillReceiveProps(nextProps) {
          if (nextProps.lng !== this.props.lng) {
            this.loadLocales(nextProps)
          }
        }

        render() {
          return <TranslatedComponent {...this.props} />
        }
      }
    )
  }
)

export default translate

export { 
  translate,
  Trans
}


