import React from "react"
import { withRouteData, Head } from "react-static"
import defaultThumbnail from "linergy-de/img/linergy-logo.svg"

const defaultKeywords = "Lithium, Akku, E-Bike, Pedelec, Umbausatz, Fahrradakku, Motor, Elektromotor, Elektrofahrrad,Ladegerät, BMS, Controller, LiIon"
const defaultDescription = "Die Linergy Engineering GmbH ist Ihr Spezialist für Elektromobilität und individuelle Akkulösungen."

const DefaultHead = ({ 
  lng, 
  title, 
  tags, 
  lngRoutes, 
  history, 
  description = defaultDescription,
  thumbnail = defaultThumbnail 
}) => {
  tags = tags || []
  const pageTitle = title ? `${title} - Linergy` : "Linergy"
  const { location: { pathname } } = history
  return (
    <Head>
      <html lang={lng || "de"} />
      <title itemProp="name" lang={lng || "de"}>
        {pageTitle}
      </title>
      <meta 
        name="keywords" 
        content={`${defaultKeywords}, ${tags.join(",")}`}
      />
      {
        lngRoutes ?
          Object.getOwnPropertyNames(lngRoutes).map(lng => (
            <link 
              key={lngRoutes[lng]} 
              rel="alternate" 
              hrefLang={lng} 
              href={lngRoutes[lng]}
            />
          )) : 
          null
      }
      <meta property="og:title" content={pageTitle} itemProp="name" />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={`https://linergy.de/${pathname}`} itemProp="url" />
      <meta property="og:description" content={description} name="description" />
      <meta property="og:image" itemProp="image" content={`https://linergy.de${thumbnail}`} />
    </Head>
  )
}

export default withRouteData(DefaultHead)


