import React from "react"
import { CircleIcon } from "linergy-de/components"
import Icon from "rmwc/Icon"

const AdocHeader = ({ showHeader, title }) => {
  if (!showHeader || !title) {
    return null
  }
  return (
    <h1 style={{ 
      paddingBottom: "15px", 
      borderBottom: "1px solid rgba(0,0,0,.15)",
      display: "flex",
      alignItems: "center"
    }}>
      {title}
      <CircleIcon 
        color="#B0BEC5" 
        style={{ marginLeft: "auto" }}
        role="button"
        title="Drucken"
        onClick={() => window.print()}
      >
        <Icon>print</Icon>
      </CircleIcon>
    </h1>
  )
}

export default AdocHeader


