import React from "react"
import { Row, GridCell } from "linergy-de/components"
import { TextField } from "rmwc/TextField"
import { Select } from "rmwc/Select"

const SpeedDisplay = ({ speed }) => (
  <div
    style={{ padding: "5px", marginTop: "1em", textAlign: "center" }}
    className="mdc-elevation--z1"
  >
    {speed ? `${speed} km/h` : "-"}
  </div>
)

class SpeedCalculator extends React.Component {
  constructor(props) {
    super(props)
    this.state = { rpm: "", d: "", voltage: "" }
    this.handleChange = this.handleChange.bind(this)
    this.calculateSpeed = this.calculateSpeed.bind(this)
  }

  handleChange(event) {
    const target = event.target
    const value = target.type === "checkbox" ? target.checked : target.value
    const name = target.name

    this.setState({
      [name]: value.replace(/[^0-9.,]/, "").replace(",", ".")
    })
  }

  setVoltage(voltage) {
    this.setState({ voltage })
  }

  calculateSpeed() {
    if (!this.state.voltage) {
      return null
    }
    const { rpm, d, voltage } = this.state
    const speed = Math.round(
      rpm * d * 2.54 * 0.0006 * Math.PI * (voltage === "48" ? 1.3 : 1)
    )
    return speed || null
  }

  render() {
    return (
      <div id="speed-calculator">
        <Row grid>
          <GridCell span="12">
            <div>
              <Select
                name="v"
                value={this.state.voltage}
                onChange={e => this.setVoltage(e.target.value)}
                box
                style={{ width: "100%" }}
                label="Akkuspannung"
                options={{ 36: "36V", 48: "48V" }}
              />
            </div>
            <div>
              <TextField
                name="d"
                box
                className="fill-width"
                label="Felgendurchmesser in Zoll"
                onChange={this.handleChange}
                value={this.state.d}
              />
            </div>
            <div>
              <TextField
                name="rpm"
                box
                className="fill-width"
                label="Drehzahl in U/min"
                onChange={this.handleChange}
                value={this.state.rpm}
              />
            </div>
            <p>
              <strong>Geschwindigkeit</strong>
            </p>
            <strong>
              <SpeedDisplay speed={this.calculateSpeed()} />
            </strong>
          </GridCell>
        </Row>
      </div>
    )
  }
}

export default SpeedCalculator




