import React from "react"

import { Row, translate, GridCell, Trans } from "linergy-de/components"

const NotFound404 = () => (
  <div id="404">
    <Row grid>
      <GridCell span="8">
        <Trans i18nKey="404">
          <h1>404 - Seite nicht gefunden</h1>
          <p>
            Wir konnten die angeforderte Seite nicht finden. Entweder sind Sie einem
            ungültigen Link gefolgt, oder die Seite existiert nicht mehr.
          </p>
        </Trans>
      </GridCell>
      <GridCell span="4">
        <div style={{
          width: "250px",
          height: "250px",
          backgroundColor: "#c60606",
          borderRadius: "50%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          color: "white",
          fontSize: "96px"
        }} className="mdc-elevation--z4">404</div>
      </GridCell>
    </Row>
  </div>
)

export default translate("404")(NotFound404)


