import React from "react"

const Row = ({ children, className, id, style, grid, tag, innerStyle }) => {
  const TheTag = tag || "div"
  return (
    <TheTag 
      id={id} 
      className={`${className || ""} ${grid ? "mdc-layout-grid" : ""} row`} 
      style={style}
    >
      <div 
        className={`row__inner ${grid ? "mdc-layout-grid__inner" : ""}`}
        style={innerStyle}
      >
        {children}
      </div>
    </TheTag>
  )
}

export default Row


