import React from "react"
import { Row, GridCell, translate, Trans, SEO } from "linergy-de/components"

import Bullet from "./Bullet"
// images
import jobsBullet from "./img/jobs.svg"
import universityBullet from "./img/university.svg"

import "./jobs.css"

const Jobs = translate("jobs")(({ t }) => (
  <div id="partners">
    <SEO image={jobsBullet} />
    <Row className="bg--beige-striped" grid>
      <GridCell span="8">
        <Trans i18nKey="intro">
          <h1>Offene Stellen</h1>
          <p>
            Im Folgenden finden Sie offene Stellen bei der Linergy Engineering GmbH. 
          </p>
        </Trans>
      </GridCell>
      <GridCell tablet="8" id="job-bullets">
        <Bullet img={jobsBullet} />
        <Bullet img={universityBullet} />
      </GridCell>
    </Row>
    <Row grid className="border-top">
      <GridCell span="12">
        {t("noJobsAvailable", "Zur Zeit sind keine Stellen zu besetzen.")}
      </GridCell>
    </Row>
  </div>
))

export default Jobs


