import React from "react"
// syntax highlighting
import Prism from "prismjs"
import "prismjs/themes/prism.css"
import "prismjs/plugins/line-numbers/prism-line-numbers.min"
import "prismjs/plugins/autoloader/prism-autoloader.min"
import "prismjs/plugins/keep-markup/prism-keep-markup.min"

import "katex/dist/katex.min.css" // For math display
import renderMathInElement from "katex/dist/contrib/auto-render.min"

import AdocHeader from "./AdocHeader"
import "./linergy.css" // Asciidoctor stylesheet
import "./line-numbers.css"


if (Prism && Prism.plugins.autoloader) {
  Prism.plugins.autoloader.languages_path = "/prismjs/components/"
}
class Adoc extends React.Component {
  render() {
    const article = this.props.article || { header: {}, html: null }
    const Tag = this.props.tag || "div"
    return (
      <Tag className="adoc" ref={e => this.article = e}>
        <AdocHeader title={article.header.doctitle} showHeader={this.props.showHeader} />
        <div itemProp="articleBody" dangerouslySetInnerHTML={{ __html: article.html }} />
      </Tag>
    )
  }

  shouldComponentUpdate(nextProps) {
    return this.props.article.header.docname !== nextProps.article.header.docname
  }

  componentDidMount() {
    const pres = this.article.querySelectorAll("div.line-numbers pre")
    for (let i = 0, l = pres.length; i < l; i++) {
      pres[i].classList.add("line-numbers")
    }
    Prism.highlightAllUnder(this.article)
    renderMathInElement(this.article, {
      delimiters: [
        { left: "\\(", right: "\\)", display: false }
      ]
    })
  }
}

export default Adoc


