import React from "react"

const LiIcon = ({ id }) => (
  <div
    id={id} 
    className="li-icon"
    style={{
      width: "120px",
      height: "120px",
      border: "6px solid #333",
      borderRadius: "50%",
      display: "inline-block",
      fontSize: "70px",
      color: "#c60606",
      fontWeight: "300",
      position: "relative",
      lineHeight: "120px",
      textAlign: "center"
    }}
  >
    Li
    <span 
      className="li-icon__plus"
      style={{
        position: "absolute",
        right: "-30px",
        top: "-30px",
        fontSize: "70px",
        lineHeight: "normal"
      }}
    >
      +
    </span>
  </div>
)

export default LiIcon


