import React from "react"
import { NavLink, withRouteData } from "react-static"

const HorizontalMenuSecondary = ({ menu }) => {
  if (!menu[1]) {
    return null 
  }
  return (
    <nav className="h-nav__secondary align-right valign-center">
      <ul className="h-nav__links">
        {
          menu[1].map(entry => (
            <li key={entry[0]} className="h-nav__link">
              <NavLink to={entry[0]} exact={entry[0].indexOf("#") === -1}>{entry[1]}</NavLink>
            </li>
          ))
        }
      </ul>
    </nav>
  )
}

export default withRouteData(HorizontalMenuSecondary)


