import React from "react"
import { translate, Trans, GridCell, Row, SEO } from "linergy-de/components"
import PartnerLogo from "./PartnerLogo"

// images
import a123Logo from "./img/a123.jpg"
import lgLogo from "./img/lg.png"
import panasonicLogo from "./img/panasonic.jpg"
import samsungLogo from "./img/samsung.jpg"
import sanyoLogo from "./img/sanyo.jpg"
import sharpLogo from "./img/sharp.jpg"
import sonyLogo from "./img/sony.jpg"

const Partners = () => (
  <div id="partners">
    <SEO image={panasonicLogo} />
    <Row grid>
      <GridCell span="12">
        <Trans i18nKey="intro">
          <h1>Unsere Partner</h1>
          <p>
            Für die gleichbleibend hohe Qualität unserer Produkte sind wir auf 
            zuverlässige und professionelle Geschäftspartner angewiesen. Aus 
            diesem Grund verarbeiten wir ausschließlich Produkte namhafter 
            Lieferanten.
          </p>
        </Trans>
      </GridCell>
      <PartnerLogo img={a123Logo} alt="A123" />
      <PartnerLogo img={lgLogo} alt="LG" />
      <PartnerLogo img={panasonicLogo} alt="Panasonic" />
      <PartnerLogo img={samsungLogo} alt="Samsung" />
      <PartnerLogo img={sanyoLogo} alt="Sanyo" />
      <PartnerLogo img={sharpLogo} alt="Sharp" />
      <PartnerLogo img={sonyLogo} alt="Sony" />
    </Row>
  </div>
)

export default translate("partners")(Partners)


