import React from "react"
import { Link } from "react-static"
import { pageSuffix } from "linergy-de/shared"

const pageCutoff = 2

function getPages(page, pageCount) {
  const cutoff = pageCutoff || pageCount
  const pages = []
  let start = page - cutoff
  if (start < 0) {
    start = 0
  }
  let end = page + cutoff
  if (end >= pageCount) {
    end = pageCount - 1
  }
  for (let i = start; i <= end; i++) {
    pages.push(i)
  }
  return pages
}

const PageLink = ({ page, children, title, url }) => (
  <Link 
    to={`${url}${pageSuffix(page)}`}
    title={title || `Gehe zu Seite ${page + 1}`}
  >
    {children || (page + 1)}
  </Link>
)

const Prev = ({ page, url }) => {
  const isVisible = page > 0
  if (!isVisible) {
    return null
  }
  return (
    <div className="pagination__prev">
      <PageLink url={url} page={page - 1}>
        ←
      </PageLink>
    </div>
  )
}

const Next = ({ page, pageCount, url }) => {
  const isVisible = (page < pageCount - 1)
  if (!isVisible) {
    return null
  }
  return (
    <div className="pagination__next">
      <PageLink url={url} page={page + 1}>
        →
      </PageLink>
    </div>
  )
}

const FastBackward = ({ pages, url }) => {
  const hasMoreBefore = (pages[0] > 0)
  if (!hasMoreBefore) {
    return null
  }
  return (
    <li key="fbw" className="pagination__page">
      <PageLink url={url} page={pages[0] - 1}>
        …
      </PageLink>
    </li>
  )
}

const FastForward = ({ pages, pageCount, url }) => {
  const lastVisiblePage = pages[pages.length - 1]
  const hasMoreAfter = (lastVisiblePage < pageCount - 1)
  if (!hasMoreAfter) {
    return null
  }
  return (
    <li key="ffw" className="pagination__page">
      <PageLink url={url} page={lastVisiblePage + 1}>
        …
      </PageLink>
    </li>
  )
}



const Pages = ({ page, pageCount, url }) => {
  const isVisible = (pageCount > 1)
  if (!isVisible) {
    return null
  }
  const pages = getPages(page, pageCount)
  return (
    <ul className="pagination__pages">
      <FastBackward url={url} pages={pages} />
      {
        pages.map(i => (
          <li 
            key={i} 
            className={`pagination__page ${page == i ? "pagination__page--current" : ""}`}
          >
            <PageLink url={url} page={i} />
          </li>
        ))
      }
      <FastForward url={url} pages={pages} pageCount={pageCount} />
    </ul>
  )
}

const Pagination = (props) => {
  return (
    <div className="pagination">
      <Prev {...props} />
      <Pages {...props} />
      <Next {...props} />
    </div>
  )
}

export default Pagination


