import React from "react"
import Select from "rmwc/Select"
import { withRouteData } from "react-static"

const availableLanguages = {
  "de": "Deutsch",
  "en": "English"
}

const LanguageSwitcher = ({ lng, lngRoutes, history }) => (
  <Select
    style={{ width:"100%" }}
    value={lng}
    options={availableLanguages}
    onChange={({ target: { value: newLng } }) => {
      if (lngRoutes && lngRoutes[newLng]) {
        return history.push(lngRoutes[newLng])
      }
      // if we do not know the equivalent route, go to the root route
      history.push("/" + newLng + "/")
    }}
  />
)

export default withRouteData(LanguageSwitcher)


