import React from "react"
import { Row, GridCell, translate, Trans } from "linergy-de/components"
import B2BForm from "./B2BForm"

const B2B = () => (
  <div id="b2b">
    <Row grid>
      <GridCell span="12">
        <Trans i18nKey="intro">
          <h1>Linergy B2B Wiederverkaufsprogramm</h1>
          <p>
          Seit einigen Jahren bieten wir unser Sortiment zu Händlerpreisen an 
          Wiederverkäufer an. Sie haben nun die Möglichkeit einen Händleraccount in 
          unserem Webshop zu erhalten. Dadurch können Sie zu besonders günstigen 
          Konditionen unsere Produkte erwerben.
          </p>
          <h2>Ihre Vorteile</h2>
          <ul>
            <li>Kein Importrisiko</li>
            <li>Wir übernehmen die kostspielige Registrierung unserer Produkte, die für kleine Unternehmen oftmals eine unüberwindbare Hürde darstellt; Stichwort "EAR-Produktregistrierung" und "Batteriegesetz". Viele Konkurrenten scheuen diese zeit- und kostenintensiven Registrierungen. Im Falle des Falles sind Sie als Händler ebenso haftbar und gelten als Importeur! Bußgeldbescheide bis 50000 EUR sind möglich</li>
            <li>Online Verfügbarkeitsanzeige unserer Produkte</li>
            <li>Nach Absprache sind bei bestimmten Produkten erweiterte Garantieleistungen möglich</li>
            <li>Kein Mindestbestellwert</li>
          </ul>
        </Trans>
      </GridCell>
    </Row>
    <Row grid className="border-top">
      <GridCell span="12">
        <h2><Trans i18nKey="contactUs">Kontaktieren Sie uns</Trans></h2>
        <B2BForm />
      </GridCell>
    </Row>
  </div>
)

export default translate("b2b")(B2B)


