import React from "react"
import "./circleicon.css"

const CircleIcon = ({ children, className, color, ...rest }) => (
  <div 
    className={`circle-icon ${className || ""}`}
    {...rest}
  >
    <div className="circle-icon__bg" style={{ backgroundColor: color }} />
    {children}
  </div>
)

export default CircleIcon


