import React from "react"
import Icon from "rmwc/Icon"

const RotationToggle = ({ label, icon, active, className, ...rest }) => (
  <span
    role="button"
    aria-label={label}
    tabIndex="0"
    className={`
      rotation-toggle
      ${active ? "rotation-toggle--active" : ""}
      ${className || ""}
    `.trim()}
    {...rest}
  >
    <Icon>{icon}</Icon>
  </span>
) 

export default RotationToggle


