import React from "react"
import { 
  Row, 
  GridCell, 
  translate, 
  Post, 
  GridInner,
  NotAvailableInLng,
} from "linergy-de/components"
import { propsToArray } from "linergy-de/shared"
import "./blog.css"
import BlogTags from "./BlogTags"
import BlogAuthors from "./BlogAuthors"
import BlogSearch from "./BlogSearch"
import Pagination from "./Pagination"

const Blog = ({ 
  page, 
  pageCount, 
  blogTags, 
  currentTag,
  currentAuthor,
  blogAuthors,
  ...rest 
}) => {
  const posts = propsToArray(rest, "post")
  let baseUrl = "/blog"
  if (currentTag) {
    baseUrl = `/blog/tag/${currentTag}`
  } else if (currentAuthor) {
    baseUrl = `/blog/author/${currentAuthor}`
  }
  const isSinglePost = (posts.length === 1)
  return (
    <div id="blog">
      <Row grid>
        <GridCell span="8">
          <GridInner>
            <NotAvailableInLng availableIn={["de"]} />
            {
              posts.map((post, i) => (
                <GridCell 
                  span="12" 
                  key={post.article.header.doctitle + i} className="mdc-elevation--z1"
                >
                  <Post {...post} showCommentsByDefault={isSinglePost} />
                </GridCell>
              ))
            }
            <GridCell span="12">
              <Pagination page={page} pageCount={pageCount} url={baseUrl} />
            </GridCell>
          </GridInner>
        </GridCell>
        <GridCell desktop="4" tablet="8">
          <GridInner>
            <GridCell 
              className="mdc-elevation--z1"
              desktop="12"
              tablet="4"         
            >
              <BlogTags tags={blogTags} currentTag={currentTag} />
            </GridCell>
            <GridCell
              className="mdc-elevation--z1"
              desktop="12"
              tablet="4"
            >
              <BlogAuthors authors={blogAuthors} currentAuthor={currentAuthor} />
            </GridCell>
            <GridCell
              className="mdc-elevation--z1"
              span="12"
            >
              <BlogSearch />
            </GridCell>
          </GridInner>
        </GridCell>
      </Row>

    </div>
  )
}

export default translate("blog")(Blog)


