import React from "react"
import { translate, Trans, Row, GridCell } from "linergy-de/components"
import Icon from "rmwc/Icon"

const ThankYou = () => (
  <div id="thank-you">
    <Row grid>
      <GridCell span="8">
        <Trans i18nKey="thankYou">
          <h1>Danke für Ihre Nachricht</h1>
          <p>
            Wir werden Ihr Anliegen bearbeiten und uns schnellstmöglich bei
            Ihnen melden.
          </p>
        </Trans>
      </GridCell>
      <GridCell span="4">
        <div
          style={{
            width: "180px",
            height: "180px",
            backgroundColor: "#c60606",
            borderRadius: "50%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            color: "white"
          }}
          className="mdc-elevation--z4"
        >
          <Icon style={{ fontSize: "96px" }}>mail</Icon>
        </div>
      </GridCell>
    </Row>
  </div>
)

export default translate("thankyou")(ThankYou)



