import React from "react"

import { Row, GridCell } from "linergy-de/components"
import { TabBar, Tab, TabIcon, TabIconText } from "rmwc/Tabs"
import SpeedCalculator from "./SpeedCalculator"
import RpmCalculator from "./RpmCalculator"

const TabBody = ({ tab }) => {
  if (tab === 0) {
    return <SpeedCalculator />
  } else {
    return <RpmCalculator />
  }
}

class MotorCalculator extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      activeTabIndex: 0
    }
  }

  render() {
    return (
      <div id="motor-calculator">
        <Row grid>
          <GridCell span="12">
            <h1>Motorenrechner</h1>
            <p>
              Welche maximale Geschwindigkeit kann ich mit der Drehzahl meines
              Motors erreichen und umgekehrt? Nutzen Sie hierfür ganz einfach
              unseren Motorenrechner.
            </p>
            <div
              className="mdc-elevation--z1"
              style={{ maxWidth: "400px", marginTop: "2em", paddingTop: "1em" }}
            >
              <TabBar
                activeTabIndex={this.state.activeTabIndex}
                onChange={evt =>
                  this.setState({ activeTabIndex: evt.target.value })
                }

              >
                <Tab>
                  <TabIcon>directions_bike</TabIcon>
                  <TabIconText>Geschwindigkeit</TabIconText>
                </Tab>
                <Tab>
                  <TabIcon>slow_motion_video</TabIcon>
                  <TabIconText>Drehzahl</TabIconText>
                </Tab>
              </TabBar>
              <TabBody tab={this.state.activeTabIndex} />
            </div>
          </GridCell>
        </Row>
      </div>
    )
  }
}

export default MotorCalculator



