import React from "react"
import { InnerRow, translate, SearchField } from "linergy-de/components"

class SearchBar extends React.Component {
  componentDidUpdate({ open: wasOpen }) {
    if (!wasOpen && this.props.open) {
      setTimeout(() => this.searchField.focus(), 50)
    }
  }

  render() {
    const { open, t, onSearch } = this.props
    return (
      <InnerRow 
        className={`
          h-nav__row
          ${open ? "" : "h-nav__row--hidden"}
        `}
        style={{ marginTop: "8px" }}
      >
        <SearchField
          box
          className="fill-width"
          label={t("searchLabel", "In Webseite suchen")}
          searchRef={r => this.searchField = r}
          onSearch={onSearch}
        />
      </InnerRow>
    )
  }
}

export default translate("search")(SearchBar)


