import React from "react"
import { Trans, translate } from "linergy-de/components/translate"
import { Link } from "react-static"
import Checkbox from "rmwc/Checkbox"
import { TextFieldHelperText } from "rmwc/TextField"

const AckDataProtection = ({ t, data, handleChange, errors }) => (
  <div>
    <p>
      <Trans i18nKey="dataProtection">
        Wir verwenden Ihre Daten ausschließlich zur Bearbeitung Ihres Anliegens. 
        Wir benötigen Ihre E-Mail Adresse, um mit Ihnen in Kontakt zu treten.
        Weitere Informationen und Widerrufshinweise finden Sie in der <Link to="/datenschutz" style={{ fontWeight: "400" }}>Datenschutzerklärung</Link>.
      </Trans>
    </p>
    <div style={{ marginBottom: "20px" }}>
      <Checkbox
        name="ackDataProtection"
        label={t("ackDataProtection", "Ich erkläre mich einverstanden, dass meine Daten zur Bearbeitung meines Anliegens verwendet werden. Die Datenschutzerklärung habe ich zur Kenntnis genommen.")}
        value={data.ackDataProtection}
        onChange={handleChange}
      />
      <TextFieldHelperText persistent validationMsg>
        {errors.ackDataProtection}
      </TextFieldHelperText>
    </div>
  </div>
)

export default translate("form")(AckDataProtection)





