import React from "react"
import { SEO, Row, GridCell } from "linergy-de/components"
import { TextField, TextFieldHelperText } from "rmwc/TextField"

import motorImg from "./motor.svg"
import nabeImg from "./nabe_normal.svg"

const SpokeDisplay = ({ spoke }) => (
  <div
    style={{ padding: "5px", marginTop: "1em", textAlign: "center" }}
    className="mdc-elevation--z1"
  >
    {spoke ? `${spoke} mm` : "-"}
  </div>
)
class SpokeCalculator extends React.Component {
  constructor(props) {
    super(props)
    this.state = { rimd: "", holed: "", a: "", k: "", n: "" }
    this.handleChange = this.handleChange.bind(this)
    this.calculateSpoke = this.calculateSpoke.bind(this)
  }

  handleChange(event) {
    const target = event.target
    const value = target.type === "checkbox" ? target.checked : target.value
    const name = target.name

    this.setState({
      [name]: value.replace(/[^0-9.,]/, "").replace(",", ".")
    })
  }

  calculateSpoke() {
    const { a, holed, rimd, k, n } = this.state

    const spoke = Math.round(
      Math.sqrt(
        a * a +
          holed * holed / 4 +
          (0.5 * rimd - 1) * (0.5 * rimd - 1) -
          holed * (0.5 * rimd - 1) * Math.cos(4 * Math.PI * k / n)
      )
    )
    return spoke
  }

  render() {
    return (
      <div id="spoke-calculator">
        <Row grid>
          <GridCell span="12">
            <h1>Speichenrechner</h1>
            <p>
              Wie kann ich für meine Felge/Nabe-Kombination die richtige
              Speichenlänge ausrechnen? Dafür können Sie den Linergy
              Speichenrechner verwenden.
            </p>
          </GridCell>
          <GridCell span="7">
            <div
              className="mdc-elevation--z1"
              style={{ padding: "24px", paddingTop: "1em" }}
            >
              <div>
                <TextField
                  name="rimd"
                  box
                  className="fill-width"
                  label="D (mm)"
                  onChange={this.handleChange}
                  value={this.state.rimd}
                />
                <TextFieldHelperText persistent>
                  <strong>D:</strong> Felgenwirkdurchmesser: Abstand der
                  Speichennippelenden an zwei gegenüberliegenden Felgenlöchern
                </TextFieldHelperText>
              </div>
              <div>
                <TextField
                  name="holed"
                  box
                  className="fill-width"
                  label="d (mm)"
                  onChange={this.handleChange}
                  value={this.state.holed}
                />
                <TextFieldHelperText persistent>
                  <strong>d:</strong> Durchmesser des Lochkreises am
                  Nabenflansch
                </TextFieldHelperText>
              </div>
              <div>
                <TextField
                  name="a"
                  box
                  className="fill-width"
                  label="a (mm)"
                  onChange={this.handleChange}
                  value={this.state.a}
                />
                <TextFieldHelperText persistent>
                  <strong>a:</strong> Abstand Nabenflasch zu Nabenmitte
                </TextFieldHelperText>
              </div>
              <div>
                <TextField
                  name="k"
                  box
                  className="fill-width"
                  label="k"
                  onChange={this.handleChange}
                  value={this.state.k}
                />
                <TextFieldHelperText persistent>
                  <strong>k:</strong> Anzahl der Speichenkreuzungen
                </TextFieldHelperText>
              </div>
              <div>
                <TextField
                  name="n"
                  box
                  className="fill-width"
                  label="n"
                  onChange={this.handleChange}
                  value={this.state.n}
                />
                <TextFieldHelperText persistent>
                  <strong>n:</strong> Anzahl der Speichen
                </TextFieldHelperText>
              </div>
              <p>
                <strong>Speichenlänge</strong>
              </p>
              <strong>
                <SpokeDisplay spoke={this.calculateSpoke()} />
              </strong>
            </div>
          </GridCell>
          <GridCell span="5">
            <div>
              <SEO image={motorImg} />
              <img
                src={motorImg}
                style={{
                  maxWidth: "100%"
                }}
                alt="Technische Zeichnung Motor"
              />
              <img
                src={nabeImg}
                style={{
                  maxWidth: "100%"
                }}
                alt="Technische Zeichnung Nabe"
              />
            </div>
          </GridCell>
        </Row>
      </div>
    )
  }
}

export default SpokeCalculator



