import React from "react"

const AvatarImage = ({ author, authorMeta }) => {
  const { avatar } = authorMeta
  if (!avatar) {
    return (
      <div 
        className="avatar-bullet"
        alt={author}
      >
        {author.authorinitials}
      </div>
    )
  }
  return (
    <img 
      className="avatar-image"
      alt={author}
      src={avatar}
    />
  )
}

export default AvatarImage


