/*global google*/

import React from "react"
import { Head } from "react-static"

const apiKey = "AIzaSyAPoWoYVaMFVBuDaKtKBss8CqO4GWg7tA4"

let gmapsResolve = null
const gmapsReady = new Promise(resolve => (gmapsResolve = resolve))

class GMapsWidget extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
    if (typeof window !== "undefined" && window.google !== undefined) {
      gmapsResolve()
    }
    
    if (typeof window !== "undefined") {
      window.initGMaps = () => gmapsResolve()
    }
  }

  render() {
    return (
      <div
        id={this.props.id}
        className="gmaps"
        ref={el => (this.state.el = el)}
        style={{
          width: "300px",
          height: "300px",
          backgroundColor: "grey"
        }}
      >
        <Head>
          <script
            id="gmaps-script"
            key="gmaps-script"
            defer
            async
            src={`https://maps.googleapis.com/maps/api/js?key=${apiKey}&callback=initGMaps`}
          />
        </Head>
      </div>
    )
  }

  async componentDidMount() {
    await gmapsReady
    const center = { lat: this.props.lat, lng: this.props.lng }
    this.state.map = new google.maps.Map(this.state.el, {
      center: center,
      zoom: this.props.zoom || 8
    })
    this.state.marker = new google.maps.Marker({
      position: center,
      map: this.state.map,
      label: this.props.label
    })
  }
}

export default GMapsWidget



