import React from "react"
import { NavLink, Link, withRouteData } from "react-static"
import {
  Drawer,
  DrawerHeader,
  DrawerContent
} from "rmwc/Drawer"
import logoImg from "linergy-de/img/linergy-logo.svg"
import DrawerMenuSecondary from "./DrawerMenuSecondary"
import RotationToggle from "../RotationToggle"
import "./style.css"

class DrawerMenu extends React.Component {
  state = {
    open: false
  }

  constructor(props) {
    super(props)
    this.toggle = this.toggle.bind(this)
  }

  toggle(e) {
    e.preventDefault()
    this.setState(prevState => ({ open: !prevState.open }))
  }

  handleClick = () => {
    this.setState({ open: false })
  }

  render() {
    const { menu, lng } = this.props
    const home = lng === "de" ? "/" : `/${lng}/`
    return (
      <nav className="drawer-nav">
        <RotationToggle
          id="menu-toggle"
          icon="menu"
          label="Menu"
          active={this.state.open}
          onClick={this.toggle}
        />
        <Drawer
          temporary
          open={this.state.open} 
          onClose={() => this.setState({ open: false })}
        >
          <DrawerHeader className="drawer-nav__header">
            <Link to={home}>
              <img src={logoImg} className="drawer-nav__logo" alt="linergy" />
            </Link>
          </DrawerHeader>
          <DrawerContent className="drawer-nav__content">
            <ul className="drawer-nav__links-primary">
              {
                menu[0].map(entry => (
                  <li key={entry[0]}>
                    <NavLink to={`${entry[0]}`} onClick={this.handleClick}>
                      {entry[1]}
                    </NavLink>
                    <DrawerMenuSecondary handleClick={this.handleClick} parentPath={entry[0]} menu={entry[2]} />
                  </li>
                ))
              }
            </ul>
          </DrawerContent>
        </Drawer>
      </nav>
    )
  }
}

export default withRouteData(DrawerMenu)


