import React from "react"
import { Link, withRouteData } from "react-static"
import Icon from "rmwc/Icon"
import "./style.css"

const Breadcrumbs = ({ breadcrumbs, lng }) => {
  const home = lng === "de" ? "/" : `/${lng}/`
  return (
    <ul className="h-breadcrumbs">
      <li key="home">
        <Link to={home}><Icon>home</Icon></Link>
      </li>
      {
        breadcrumbs.map((crumb, i) => {
          return ([
            <li key={`c${i}`}><Icon>chevron_right</Icon></li>,
            <li key={i}>
              <Link to={crumb[0]}>{crumb[1]}</Link>
            </li>
          ])
        })
      }
    </ul>
  )
}

export default withRouteData(Breadcrumbs)


