import React from "react"
import TextField, { TextFieldHelperText } from "rmwc/TextField"
import { withFormMailer } from "linergy-de/components"
import i18n from "linergy-de/i18n"

const t = i18n.getFixedT(null, "b2b")

function validateEmail(email) {
  const re = /\S+@\S+\.\S+/
  return re.test(email)
}

const B2BForm = withFormMailer({
  to: "info@linergy.de",
  data: {
    subject: "B2B Händleranfrage von linergy.de",
    email: "",
    company: "",
    address: "",
    message: ""
  },
  validate: function (data) {
    const errors = {}
    let error = false
    if (!validateEmail(data.email)) {
      errors.email = t("emailInvalid", "E-Mail Adresse ist ungültig")
      error = true
    }
    if (data.message.length <= 10) {
      errors.message = t("messageTooShort", "Sie müssen mindestens zehn Zeichen eingeben")
      error = true
    }
    /*if (!this.state.company) {
      errors.company = t("companyRequired", "Sie müssen eine Firmenbezeichnung eingeben")
      error = true
    }*/
    /*if (!this.state.address.length <= 10) {
      errors.address = t("addressRequired", "Sie müssen eine Adresse eingeben")
      error = true
    }*/
    return error ? errors : null
  }
})(({ handleChange, data, errors }) => (
  <div>
    <TextField
      name="company"
      label={t("companyName", "Firmenname inkl. Rechtsform")}
      className="fill-width"
      box
      onChange={handleChange}
      value={data.company}
    />
    <TextFieldHelperText persistent validationMsg>
      {errors.company}
    </TextFieldHelperText>
    <TextField
      name="email"
      label={t("yourEmail", "Ihre E-Mail Adresse")}
      box
      className="fill-width"
      onChange={handleChange}
      value={data.email}
    />
    <TextFieldHelperText persistent validationMsg>
      {errors.email}
    </TextFieldHelperText>
    <TextFieldHelperText persistent>
      {t("isRequired", "Dieses Feld muss ausgefüllt werden")}
    </TextFieldHelperText>
    <TextField
      name="address"
      textarea
      placeholder={t("yourAddress", "Ihre Postanschrift")}
      rows="4"
      fullwidth
      onChange={handleChange}
      value={data.address}
    />
    <TextFieldHelperText persistent validationMsg>
      {errors.address}
    </TextFieldHelperText>
    <TextField
      name="message"
      textarea
      fullwidth
      placeholder={t("yourMessage", "Ihre Nachricht")}
      rows="8"
      onChange={handleChange}
      value={data.message}
    />
    <TextFieldHelperText persistent validationMsg>
      {errors.message}
    </TextFieldHelperText>
    <TextFieldHelperText persistent>
      {t("isRequired", "Dieses Feld muss ausgefüllt werden")}
    </TextFieldHelperText>
  </div>
))

export default B2BForm


