import React from "react"
import { Row, translate, Trans, GridCell } from "linergy-de/components"

import Icon from "rmwc/Icon"

import GMapsWidget from "./GMapsWidget"
import ContactForm from "./ContactForm"

const Contact = translate("contact")(({ t }) => (
  <div id="contact">
    <Row grid>
      <GridCell span="12">
        <h1>{t("contactTitle", "Kontaktieren Sie uns")}</h1>
      </GridCell>
      <GridCell desktop="8" tablet="5">
        <p className="first">
          <Trans i18nKey="contactBody">
            Haben Sie Fragen, Anregungen oder wollen Sie eine unsere 
            Dienstleistungen in Anspruch nehmen? Schreiben Sie uns einfach 
            eine Nachricht, wir kontaktieren Sie dann umgehend.
          </Trans>
        </p>
        <ContactForm />
      </GridCell>
      <GridCell tablet="3">
        <div id="address">
          <strong>Linergy Engineering GmbH</strong><br />
          Senserstraße 5<br />
          82140 Olching<br />
          Deutschland
        </div>
        <ul id="contact-info" style={{ 
          marginLeft: "0", 
          paddingLeft: "0",
          marginTop: "20px"
        }}>
          <li className="icon-header">
            <Icon>phone_android</Icon>+49 8142 44 88 565
          </li>
          <li className="icon-header">
            <Icon>print</Icon>+49 8142 42 39 976
          </li>
          <li className="icon-header">
            <Icon>mail</Icon><a href="mailto:info@linergy.de">info@linergy.de</a>
          </li>
        </ul>
        <GMapsWidget 
          lat={48.230364} 
          lng={11.335875} 
          zoom={8}
        />
      </GridCell>
    </Row>
  </div>
))

export default Contact


