import React from "react"
import { Router, Route, Redirect } from "react-static"
import Routes from "react-static-routes"
import i18n from "linergy-de/i18n"
import { hot } from "react-hot-loader"
// import ReactGA from "react-ga"

import "material-components-web/dist/material-components-web.min.css"
import Footer from "./Footer"
import Toolbar from "./Toolbar"
import CookieAccept from "./CookieAccept"
import LanguageDetector from "./LanguageDetector"
import DefaultHead from "./DefaultHead"

import "./app.css"
import "./backgrounds.css"

/*ReactGA.initialize("UA-116073150-1", {
  debug: process.env.REACT_STATIC_ENV === "development"
})*/ // dsgvo

/*global reactStaticGetComponentForPath*/
const App = () => (
  <Router>
    <div id="linergy-website" className="mdc-typography" key="website">
      <Routes
        render={() => (
          <Route path='*' render={props => {
            const Comp = reactStaticGetComponentForPath(props.location.pathname)
            if (!Comp) {
              const url404 = i18n.language !== "de" ? `/${i18n.language}/404` : "/404"
              return <Redirect to={url404} />
            }
            // if (typeof window !== "undefined") {
            //   ReactGA.pageview(window.location.pathname + window.location.search)
            // }
            return (
              <React.Fragment>
                <DefaultHead />
                <LanguageDetector />
                <Toolbar />
                <main id="main">
                  <Comp key={props.location.pathname} {...props} />
                </main>
                <CookieAccept />
                <Footer />
              </React.Fragment>
            )
          }} />
        )} />
    </div>
  </Router>
)

export default hot(module)(App)


