import React from "react"
import axios from "axios"
import { withRouter } from "react-static"
import Button from "rmwc/Button"
import i18n from "linergy-de/i18n"
import { translate } from "linergy-de/components/translate"

import linergyLogo from "linergy-de/img/linergy-logo.svg"
import "./formspree.css"
import AckDataProtection from "./AckDataProtection"

const FormSending = ({ sending }) => {
  if (!sending) {
    return null
  }
  return (
    <div className="formspree-form__sending">
      <div className="formspree-form__throbber mdc-elevation--z4">
        <img src={linergyLogo} alt="Linergy" style={{ width: "80%" }} />
      </div>
    </div>
  )
}

export default ({
  to,
  data,
  next,
  validate,
  beforeSend,
  afterSend,
  needAck = true
}) => {
  return FormBody => {
    return translate("form")(
      withRouter(
        class FormspreeForm extends React.Component {
          state = {
            sending: false,
            errors: {},
            data: {
              ...data,
              ackDataProtection: false
            },
            serverError: ""
          }

          handleChange = event => {
            const target = event.target
            const value =
              target.type === "checkbox" ? target.checked : target.value
            const name = target.name
            const data = { ...this.state.data }
            data[name] = value
            this.setState({
              data
            })
          }

          handleSubmit = event => {
            event.preventDefault()
            const errors1 = this.validate()
            const errors2 = validate.call(this, this.state.data)
            if (!errors1 && !errors2) {
              return this.send()
            }
            const errors = {
              ...(errors1 || {}),
              ...(errors2 || {})
            }
            this.setState({ errors })
          }

          validate() {
            const { t } = this.props
            if (needAck && this.state.data.ackDataProtection) {
              return null
            }
            return {
              ackDataProtection: t(
                "ackDataProtectionError",
                "Sie müssen den Datenschutzbestimmungen zustimmen"
              )
            }
          }

          async send() {
            const dataToSend = beforeSend ? beforeSend.call(this) : {}
            const { data } = this.state
            Object.assign(dataToSend, data)
            const {
              subject,
              email,
              ...rest
            } = data

            const message = JSON.stringify(rest, null, 2)

            try {
              this.setState({ sending: true, serverError: "" })
              await axios.post("/form-mailer", {
                subject,
                email,
                message,
                ackDataProtection: true
              })
            } catch (e) {
              return this.setState({ serverError: e.message })
            } finally {
              this.setState({ sending: false })
            }

            if (afterSend) {
              afterSend.call(this)
            }
            if (next) {
              this.props.history.push(
                typeof next === "string" ? next : next.call(this, this.props)
              )
            } else {
              this.props.history.push(
                {
                  de: "/vielen-dank",
                  en: "/en/thank-you"
                }[i18n.language]
              )
            }
          }

          render() {
            const { t } = this.props
            return (
              <form className="formspree-form" onSubmit={this.handleSubmit}>
                <FormSending sending={this.state.sending} />
                <FormBody
                  {...this.props}
                  data={this.state.data}
                  errors={this.state.errors}
                  handleChange={this.handleChange}
                  handleSubmit={this.handleSubmit}
                />
                {needAck ? (
                  <AckDataProtection
                    data={this.state.data}
                    errors={this.state.errors}
                    handleChange={this.handleChange}
                  />
                ) : null}
                <div style={{ color: "red", padding: "10px" }}>
                  {this.state.serverError}
                </div>
                <Button raised onClick={this.handleSubmit}>
                  {t("send", "Senden")}
                </Button>
              </form>
            )
          }
        }
      )
    )
  }
}



