import React from "react"
import { Row, translate, Trans, GridCell, SEO } from "linergy-de/components"
import Button from "rmwc/Button"
import { Link, withRouteData } from "react-static"
import { formatDate } from "linergy-de/shared"
import "./home.css"

import batteryImg from "../Products/img/akku.png"
import wikiImg from "./img/linergy-wiki.svg"
import notebookImg from "./img/notebook-linergy-shop.png"

const Home = ({ t, recentPosts, spotlight }) => (
  <div id="home">
    <SEO image={batteryImg} />
    <Row grid className="bg--blue-striped">
      <GridCell desktop="6" tablet="8" style={{ color: "white", paddingBottom: "12px" }}>
        <Trans i18nKey="intro">
          <h1>Linergy Power für Ihre Anwendungen</h1>
          <p>
            Mit den vielseitigen Akkuprodukten von Linergy können Sie sich 
            jederzeit auf verlässliche und leistungsstarke Technologien verlassen.
          </p>
          <p>
            Unsere Lithium-Ionen-Akkus kommen in verschiedensten Anwendungen zum 
            Einsatz: Elektromobilität, Werkzeug, medizinische Geräte, Forschung, 
            Robotik, Entwicklung, Labor und viele weitere.
          </p>
          <p>
            Wählen Sie aus unserem großen Angebot von Standardmodellen oder 
            lassen Sie sich einen maßgeschneiderten Akku, ganz nach Ihren 
            Anforderungen, konfektionieren.
          </p>
          <Link to={t("batteriesLink", "/produkte/akkus")}>
            <Button raised>Zu den Akkus</Button>
          </Link>
        </Trans>
      </GridCell>
      <GridCell desktop="6" tablet="8">
        <img src={batteryImg} style={{ maxWidth: "100%" }} alt="Akku" />
      </GridCell>
    </Row>
    <Row grid>
      <GridCell id="news" tag="section" desktop="8" tablet="8">
        <h2 className="fancy-heading">
          <span className="fancy-heading__title">
            {t("news", "Neues von Linergy")}
          </span>
          <span className="fancy-heading__more">
            <Link to="/blog" title="Blog">
              {t("more", "Mehr")} ›
            </Link>
          </span>
        </h2>
        <div className="mdc-layout-grid__inner">
          {
            recentPosts.map(({ slug, date, doctitle }) => (
              <GridCell 
                key={slug} 
                className="insight"
                itemScope
                itemType="http://schema.org/Article"
              >
                <h3 className="news-heading">
                  <Link 
                    to={`/blog/${slug}`} 
                    title={doctitle}
                    itemProp="url"
                  >
                    <span itemProp="name">{doctitle}</span>
                  </Link>
                </h3>
                <time 
                  className="insight__date"
                  dateTime={date}
                  itemProp="datePublished"
                >
                  {formatDate(date)}
                </time>
              </GridCell>
            ))
          }
        </div>
      </GridCell>

      <GridCell id="spotlight" tag="section" desktop="4" tablet="8">
        <h2 className="fancy-heading">
          <span className="fancy-heading__title">              
            {t("spotlight", "Spotlight")}
          </span>
        </h2>
        <div className="mdc-layout-grid__inner">
          <GridCell 
            className="insight"
            desktop="12"
            itemScope
            itemType="http://schema.org/Article"
          >
            <h3 className="spotlight-heading">
              <Link 
                to={spotlight.link} 
                title={spotlight.title}
                itemProp="url"
              >
                <span itemProp="name">{spotlight.title}</span>
              </Link>
            </h3>
            <time 
              className="insight__date"
              dateTime={spotlight.date}
              itemProp="datePublished"
            >
              {formatDate(spotlight.date)}
            </time>
          </GridCell>
        </div>
      </GridCell>
    </Row>

    <Row grid className="border-top">
      <GridCell desktop="6" tablet="8" className="center-tablet center-phone">
        <img src={wikiImg} alt="Linergy Wiki" style={{ maxHeight: "400px", width: "90%" }} />
      </GridCell>
      <GridCell desktop="6" tablet="8">
        <Trans i18nKey="wiki">
          <h2>
            Die Linergy Wissensdatenbank
          </h2>
          <p>
            Die Wissensdatenbank rund um die Themen 
            Akkutechnik, Elektrotechnik und Elektromobilität.
          </p>
          <p>
            Hier finden Sie Anleitungen, Tools und technische Artikel zu
            Produkten und Dienstleistungen von Linergy.
          </p>
          <Link to="/wiki" title="Wiki">
            <Button raised>Zur Linergy Wissensdatenbank</Button>
          </Link>
        </Trans>
      </GridCell>
    </Row>

    <Row grid className="border-top">
      <GridCell desktop="6" tablet="8">
        <Trans i18nKey="webshop">
          <h2>Qualität und Flexibilität - der Linergy Onlineshop</h2>
          <p>
            Wir führen über 600 verschiedene Artikel und unzählige Varianten
            in unserem Onlineshop <Link to="https://shop.linergy.de" style={{ fontWeight: "500" }}>shop.linergy.de</Link>.
          </p>
          <p>
            Lithium-Ionen Akkus, Akkus für Elektrofahrräder, Umrüstsätze für E-Bikes und Pedelec,
            Motoren, Ladegeräte und Controller finden Sie hier zu attraktiven Konditionen.
            Darüber hinaus erhalten Sie bei uns Zubehör und Produkte für Elektrotechnik, Akkukonfektion und E-Bikes.
          </p>
          <Link to="https://shop.linergy.de" title="Webshop">
            <Button raised>Zum Shop</Button>
          </Link>
        </Trans>
      </GridCell>
      <GridCell desktop="6" tablet="8" className="center-tablet center-phone">
        <img src={notebookImg} alt="Linergy Shop" style={{ maxWidth: "100%" }} />
      </GridCell>
    </Row>
    
  </div>
)

export default translate("home")(withRouteData(Home))


