import React from "react"
import { Grid } from "rmwc/Grid"
import { GridCell, Adoc } from "linergy-de/components"
import { Link } from "react-static"
import moment from "moment"
import {
  readingTimeText,
  toDate
} from "linergy-de/shared"

import authors from "./authors"
import AvatarImage from "./AvatarImage"
import ShareLinks from "./ShareLinks"
import PostComments from "./PostComments"
import "./post.css"

const Post = ({
  article, 
  pathname,
  className,
  headerTag,
  slug,
  topicId,
  showCommentsByDefault,
  ...rest
}) => {
  const { 
    header: { 
      wordcount, 
      author, 
      authorinitials, 
      keywords, 
      doctitle,
      tags,
      thumbnail = "",
      revdate
    } 
  } = article
  const date = toDate(revdate)
  const authorMeta = authors[authorinitials] || {}
  const thisUrl = `https://linergy.de/blog/${slug}`
  const HeaderTag = headerTag || "h1"
  return (
    <article
      itemScope
      itemType="http://schema.org/BlogPosting"
      className={`post ${className || ""}`.trim()} 
      {...rest}
    >
      <meta itemProp="thumbnailUrl" content={thumbnail} />
      <Grid tag="header" className="post__header">
        <GridCell desktop="8" tablet="8" className="post__author-and-meta">
          <Link to={`/blog/author/${author}`}>
            <AvatarImage author={author} authorMeta={authorMeta} />
          </Link>
          <div className="post__meta">
            <meta itemProp="keywords" content={keywords} />
            <div 
              itemProp="author"
              itemScope
              itemType="http://schema.org/Person"
              className="post__author"
            >
              <span itemProp="name">
                <Link to={`/blog/author/${author}`} itemProp="url">
                  {author}
                </Link>
              </span>
            </div>
            <div className="post__author-info">
              {authorMeta.bio || "-"}
            </div>
            <div className="post__misc">
              <time 
                itemProp="datePublished" 
                className="post__date"
                dateTime={date}
              >
                {moment(date).format("Do MMMM YYYY")}
              </time>
              <span className="post__reading-time">{readingTimeText(wordcount)}</span>
            </div>
          </div>
        </GridCell>
        <GridCell desktop="4" tablet="8" className="post__social">
          <ShareLinks url={thisUrl} doctitle={doctitle} keywords={keywords} />
        </GridCell>
      </Grid>
      <div className="post__content">
        <Link itemProp="discussionUrl" to={`/blog/${slug}`}>
          <HeaderTag 
            itemProp="headline" 
            className="post__title"
          >
            {doctitle}
          </HeaderTag>
        </Link>
        <Adoc className="post__body" article={article} />
        <ul className="post__tags">
          {
            tags.map(tag => (
              <li key={tag} className="post__tag">
                <Link to={`/blog/tag/${tag}`}>{tag}</Link>
              </li>
            ))
          }
        </ul>
      </div>
      <PostComments topicId={topicId} show={showCommentsByDefault} />
    </article>
  )
}

export default Post


