import React from "react"

import { GridCell, Row, translate, Trans, SEO } from "linergy-de/components"
import ReferenceLogo from "./ReferenceLogo"

// images
import betrandtLogo from "./img/bertrandt.gif"
import daimlerLogo from "./img/daimler.png"
import dlrLogo from "./img/dlr.png"
import hmLogo from "./img/hm.jpg"
import fhSWFLogo from "./img/fh-suedwestfalen.jpg"
import helmholtzLogo from "./img/helmholtz.jpg"
import lmuLogo from "./img/lmu.jpg"
import hsOsnabrueckLogo from "./img/osnabrueck.png"
import rwthLogo from "./img/rwth.png"
import steinbeisLogo from "./img/steinbeis.jpg"
import tumLogo from "./img/tum.png"
import uniKasselLogo from "./img/uni-kassel.jpg"

const References = () => (
  <div id="references">
    <SEO image={tumLogo} />
    <Row grid>
      <GridCell span="12">
        <Trans i18nKey="intro">
          <h1>Unsere Referenzen</h1>
          <p>Zufriedene Kunden sind unsere größte Auszeichnung. Linergy Produkte 
            werden in einer Vielzahl von Produkten und Projekten renommierter 
            Firmen erfolgreich eingesetzt.</p>
        </Trans>
      </GridCell>
      <ReferenceLogo img={betrandtLogo} alt="Betrandt" />
      <ReferenceLogo img={lmuLogo} alt="Ludwig-Maximilians-Universität" />
      <ReferenceLogo img={uniKasselLogo} alt="Uni Kassel" />
      <ReferenceLogo img={helmholtzLogo} alt="Helmholtz Association" />
      <ReferenceLogo img={dlrLogo} alt="Deutsches Zentrum für Luft- und Raumfahrt" />
      <ReferenceLogo img={rwthLogo} alt="RWTH Aachen" />
      <ReferenceLogo img={hsOsnabrueckLogo} alt="Hochschule Osnabrück" />
      <ReferenceLogo img={fhSWFLogo} alt="Fachhochschule Südwestfalen" />
      <ReferenceLogo img={tumLogo} alt="Technische Universität München" />
      <ReferenceLogo img={hmLogo} alt="Hochschule München" />
      <ReferenceLogo img={daimlerLogo} alt="Daimler" />
      <ReferenceLogo img={steinbeisLogo} alt="Steinbeis" />
    </Row>
  </div>
)

export default translate("references")(References)


