import React from "react"
import { GridCell } from "rmwc/Grid"

const PartnerLogo = ({ alt, img }) => (
  <GridCell desktop="3">
    <img className="partner-logo" alt={alt} src={img} style={{ maxWidth: "100%" }} />
  </GridCell>
)

export default PartnerLogo


