import React from "react"
import { NavLink, withRouteData } from "react-static"
import HoverMenu from "./HoverMenu"

const HorizontalMenuPrimary = ({ menu }) => (
  <nav className="h-nav__primary align-right valign-center">
    <ul className="h-nav__links">
      {
        menu[0].map(entry => (
          <li key={entry[0]} className="h-nav__link">
            <NavLink className=".h-nav__" to={`${entry[0]}`}>{entry[1]}</NavLink>
            <HoverMenu menu={entry[2]} />
          </li>
        ))
      } 
    </ul>
  </nav>
)

export default withRouteData(HorizontalMenuPrimary)


