import React from "react"
import { Row, GridCell } from "linergy-de/components"
import { TextField } from "rmwc/TextField"
import { Select } from "rmwc/Select"

const RpmDisplay = ({ rpm }) => (
  <div
    style={{ padding: "5px", marginTop: "1em", textAlign: "center" }}
    className="mdc-elevation--z1"
  >
    {rpm ? `${rpm} U/min` : "-"}
  </div>
)

class RpmCalculator extends React.Component {
  constructor(props) {
    super(props)
    this.state = { speed: "", d: "", voltage: "" }
    this.handleChange = this.handleChange.bind(this)
    this.calculateRpm = this.calculateRpm.bind(this)
  }

  handleChange(event) {
    const target = event.target
    const value = target.type === "checkbox" ? target.checked : target.value
    const name = target.name

    this.setState({
      [name]: value.replace(/[^0-9.,]/, "").replace(",", ".")
    })
  }

  setVoltage(voltage) {
    this.setState({ voltage })
  }

  calculateRpm() {
    if (!this.state.voltage) {
      return null
    }
    const { speed, d, voltage } = this.state
    const coeff = voltage === "48" ? 1.3 : 1
    const rpm = Math.round(speed / d / 2.54 * 100000 / 60 / Math.PI / coeff)
    return rpm || null
  }

  render() {
    return (
      <div id="rpm-calculator">
        <Row grid>
          <GridCell span="12">
            <div>
              <Select
                name="v"
                value={this.state.voltage}
                onChange={e => this.setVoltage(e.target.value)}
                box
                style={{ width: "100%" }}
                label="Akkuspannung"
                options={{ 36: "36V", 48: "48V" }}
              />
            </div>
            <div>
              <TextField
                name="d"
                box
                className="fill-width"
                label="Felgendurchmesser in Zoll"
                onChange={this.handleChange}
                value={this.state.d}
              />
              <div>
                <div>
                  <TextField
                    name="speed"
                    box
                    className="fill-width"
                    label="Geschwindigkeit in km/h"
                    onChange={this.handleChange}
                    value={this.state.speed}
                  />
                </div>
                <p>
                  <strong>Drehzahl</strong>
                </p>
                <strong>
                  <RpmDisplay rpm={this.calculateRpm()} />
                </strong>
              </div>
            </div>
          </GridCell>
        </Row>
      </div>
    )
  }
}

export default RpmCalculator



