import React from "react"
import { translate, Row, GridCell } from "linergy-de/components"
import { Link } from "react-static"
import Icon from "rmwc/Icon"

import {
  FacebookIcon,
  TwitterIcon,
  YoutubePlayIcon
} from "linergy-de/components/SocialIcons"

import LanguageSwitcher from "./LanguageSwitcher"
import "./footer.css"

const Footer = ({ t }) => (
  <Row tag="footer" id="footer" grid>
    <GridCell desktop="3" tablet="4">
      <h4 className="footer__header">
        {t("infos", "Infos")}
      </h4>
      <ul className="footer__links">
        <li><a href="https://www.linergy-shop.de">{t("onlineshop", "Onlineshop")}</a></li>
        <li><Link to={t("assistantLink", "/")}>{t("assistantCaption", "E-Bike Assistent")}</Link></li>
        <li><Link to="/wiki">{t("wiki", "Tools & Anleitungen")}</Link></li>
      </ul>
    </GridCell>

    <GridCell desktop="3" tablet="4">
      <h4 className="footer__header">
        {t("contact", "Kontakt")}
      </h4>
      <ul className="footer__links">
        <li className="icon-header">
          <Icon>phone_android</Icon>
          <span>+49 (0)8142 4488565</span>
        </li>
        <li className="icon-header">
          <Icon>email</Icon>
          <a href="mailto:info@linergy.de">info@linergy.de</a>
        </li>
        <li className="icon-header">
          <Icon>send</Icon>
          <Link to={t("contactLink", "/kontakt")}>{t("contactCaption", "Kontaktformular")}</Link>
        </li>
      </ul>
    </GridCell>

    <GridCell desktop="3" tablet="4">
      <h4 className="footer__header">
        {t("aboutUs", "Über uns")}
      </h4>
      <ul className="footer__links">
        <li><Link to="/datenschutz">{t("dataPrivacyDeclaration", "Datenschutzerklärung")}</Link></li>
        <li><Link to="/impressum">{t("legalNotice", "Impressum")}</Link></li>
        <li style={{ display: "flex" }}>
          <Link 
            to="https://twitter.com/linergy_de" 
            style={{ marginRight: "8px" }}
          >
            <TwitterIcon />
          </Link>
          <Link 
            to="https://www.facebook.com/Linergy.de/"
            style={{ marginRight: "8px" }}
          >
            <FacebookIcon />
          </Link>
          <Link 
            to="https://www.youtube.com/channel/UCPcfbq3keus7R7rLK6-gtug"
            style={{ marginRight: "8px" }}
          >
            <YoutubePlayIcon />
          </Link>
        </li>
      </ul>
    </GridCell>

    <GridCell desktop="3" tablet="4">
      <h4 className="footer__header">
        {t("language", "Sprache")}
      </h4>
      <LanguageSwitcher />
    </GridCell>
  </Row>
)

export default translate("footer")(Footer)


