import React from "react"
import { Row, GridCell, translate, Trans } from "linergy-de/components"
import { Link } from "react-static"
import { TextField } from "rmwc/TextField"
import { Button } from "rmwc/Button"
import axios from "axios"

import lunr from "lunr"
import lunrStemmerSupport from "lunr-languages/lunr.stemmer.support"
import lunrDe from "lunr-languages/lunr.de"
import lunrMultilanguage from "lunr-languages/lunr.multi"

import "./search.css"

lunrStemmerSupport(lunr)
lunrDe(lunr)
lunrMultilanguage(lunr)

const search = {
  indexmap: null,
  index: null,
  inflight: {},
  docmaps: {},
  documents: {}
}

function withCache({ store, inflightStore, key }) {
  return func => {
    return (...args) => {
      const _key = typeof key === "string" ? key : key(...args)
      if (store[_key]) {
        return store[_key]
      }
      if (inflightStore[_key]) {
        return inflightStore[_key]
      }
      const _func = async () => {
        const data = await func(...args)
        store[_key] = data
        return data 
      }
      inflightStore[_key] = _func()
      return inflightStore[_key]
    }
  }
}

const withSearchCache = key => withCache({ 
  store: search,
  inflightStore: search.inflight,
  key
})

const getIndexmap = withSearchCache("indexmap")(
  async () => {
    const { data: indexmap } = await axios.get("/lunr/indexmap.json?" + Date.now())
    return indexmap
  }
)

const getDocmap = withSearchCache(domain => `docmap-${domain}`)(
  async domain => {
    const { data: docmap } = await axios.get(`/lunr/docmap-${domain}.json?${Date.now()}`)
    return docmap
  }
)

const getIndex = withSearchCache("index")(
  async () => {
    const indexmap = await getIndexmap()
    const indexPath = `/lunr/indexes/${indexmap.default}.json`
    const { data: index } = await axios.get(indexPath)
    const lunrIndex = lunr.Index.load(index)
    return lunrIndex
  }
)

const loadDocument = withCache({
  store: search.documents,
  inflightStore: search.inflight,
  key: ref => ref
})(
  async ref => {
    const dochash = search["docmap-default"][ref]
    const docUrl = `/lunr/documents/${dochash}.json`
    const { data: doc } = await axios.get(docUrl)
    return doc
  }
)

function loadDocuments(results) {
  return Promise.all(
    results.map(
      ({ ref }) => loadDocument(ref)
    )
  )
}

class Search extends React.Component {
  state = {
    searchString: "",
    error: null,
    results: [],
    loading: false
  }

  constructor(props) {
    super(props)
    const searchString = this.getSearchString(props)
    this.state.searchString = searchString || ""
    this.handleChange = this.handleChange.bind(this)
    this.handleKeyPressed = this.handleKeyPressed.bind(this)
    this.search = this.search.bind(this)
    if (this.state.searchString != "") {
      this.search()
    }
  }

  getSearchString(props) {
    // get search string if set by SearchBar
    props = props || this.props
    const { history: { location: { state } } } = props
    return state ? state.searchString : undefined
  }

  handleChange({ target: { value } }) {
    this.setState({ searchString: value })
  }

  handleKeyPressed({ keyCode }) {
    if (keyCode !== 13)  {
      return
    }
    this.search()
  }

  async search() {
    const [index] = await Promise.all([
      getIndex(),
      getDocmap("default")
    ])
    const results = this._search(index)
    await loadDocuments(results)
    return this.setState({ results })
  }

  _search(index) {
    const query = this.state.searchString.trim()
    if (query.length === 0) {
      return []
    }
    return index.search(query)
  }

  render() {
    const { t } = this.props
    return (
      <div id="search">
        <Row grid>
          <GridCell span="12">
            <h1>
              <Trans i18nKey="searchTitle">
                Durchsuche Linergy Seiten
              </Trans>
            </h1>
          </GridCell>
        </Row>
        <Row grid>
          <GridCell span="10">
            <TextField
              label={t("searchExample", "z.B. LiMn")}
              box
              className="fill-width"
              withTrailingIcon="search"
              style={{ paddingLeft: "15px", paddingTop: "15px" }}
              onChange={this.handleChange}
              onKeyDown={this.handleKeyPressed}
              value={this.state.searchString}
            />
          </GridCell>
          <GridCell span="2">
            <Button
              raised
              style={{
                height: "56px",
                marginRight: "0",
                marginTop: "16px",
                marginLeft: "auto"
              }}
              onClick={this.search}
            >
              <Trans i18nKey="searchAction">Suchen</Trans>
            </Button>
          </GridCell>
          <GridCell span="12">
            <SearchResults 
              results={this.state.results}
              documents={search.documents}
            />
          </GridCell>
        </Row>
      </div>
    )
  }
}

const SearchResults = ({ results, documents }) => {
  return (<ul className="search-results">
    {results.map(
      result => (
        <SearchResult 
          key={result.ref} 
          result={result} 
          document={documents[result.ref]}
        />
      )
    )}
  </ul>)
}

function getPositions(metadata) {
  const tokens = Object.keys(metadata)
  const textPositions = []
  const titlePositions = []
  tokens.forEach(token => {
    const { text, title } = metadata[token]
    if (text) {
      const { position } = text
      // get max two occurences of each token for preview
      const l = position.length > 2  ? 2 : position.length
      for (let i = 0; i < l; i++) {
        textPositions.push(position[i])
      }
    }
    if (title) {
      const { position } = title
      Array.prototype.push.apply(titlePositions, position)
    }
  })
  return {
    textPositions,
    titlePositions: titlePositions.sort((a, b) => (a[0] - b[0]))
  }
}

function getPreviewTextFragment(text, position) {
  const [highlightStart, highlightLength] = position
  const highlightEnd = highlightStart + highlightLength
  let start = text.indexOf(" ", highlightStart - 50)
  if (start === -1) {
    start = 0
  }
  let end = text.indexOf(" ", highlightEnd + 20)
  if (end === -1) {
    end = text.length
  }
  return {
    before: text.slice(start, highlightStart),
    highlight: text.slice(highlightStart, highlightEnd),
    after: text.slice(highlightEnd, end)
  }
}

function getPreviewText(text, positions) {
  const fragments = positions.map(
    position => getPreviewTextFragment(text, position)
  )
  return fragments.map(({ before, highlight, after}, i) => (
    <span key={i} className="preview-fragment">
      &nbsp;...
      {before}
      <b>{highlight}</b>
      {after}
      ...&nbsp;
    </span>
  ))
}

function getTitleFragments(title, positions) {
  let current = 0
  const fragments = []
  positions.forEach(
    pos => {
      const end = pos[0] + pos[1]
      const normalText = title.slice(current, pos[0])
      if (normalText.length > 0) {
        fragments.push({
          text: normalText
        })
      }
      fragments.push({
        text: title.slice(pos[0], end),
        highlight: true
      })
      current = end
    }
  )
  const suffix = title.slice(current, title.length)
  fragments.push({ text: suffix })
  return fragments
}

function getTitle(title, positions) {
  const fragments = getTitleFragments(title, positions)
  return fragments.map(({ text, highlight }, i) => {
    if (highlight) {
      return <b key={i}>{text}</b>
    }
    return text
  })
}

const SearchResult = ({ result, document }) => {
  const link = `/${document.path}`
  const { matchData: { metadata } } = result
  const { textPositions, titlePositions } = getPositions(metadata)
  return (
    <li className="search-result">
      <h3>
        <Link to={link}>{getTitle(document.title || "", titlePositions)}</Link>
      </h3>
      <h4>
        <Link to={link}>{link}</Link>
      </h4>
      <ul className="search-result__tags">
        {
          (document.tags || []).map(tag => (
            <li key={tag} className="search-result__tag">{tag}</li>
          ))
        }
      </ul>
      <div className="search-result__preview">
        {getPreviewText(document.text, textPositions)}
      </div>
    </li>
  )
}

export default translate("search")(Search)


