import React from "react"
import { Link } from "react-static"
import {
  FacebookIcon,
  TwitterIcon,
  GooglePlusIcon,
  EmailIcon,
  LinkIcon
} from "../SocialIcons"

const ShareLinks = ({ url, doctitle, keywords }) => (
  <ul className="post__share">
    <li>
      <Link to={`https://www.facebook.com/sharer/sharer.php?u=${url}`} title="Auf Facebook teilen">
        <FacebookIcon />
      </Link>
    </li>
    <li>
      <Link to={`https://twitter.com/intent/tweet?text=${doctitle}&url=${url}&hashtags=${keywords}&via=linergy`}>
        <TwitterIcon />
      </Link>
    </li>
    <li>
      <Link to={`https://plus.google.com/share?url=${url}`}>
        <GooglePlusIcon />
      </Link>
    </li>
    <li>
      <Link 
        to={`mailto:?subject=${doctitle}&body=Ich habe '${doctitle}' auf linergy.de gelesen. Schau es dir doch mal an: ${url}`}
        title="Per E-Mail teilen"
      >
        <EmailIcon />
      </Link>
    </li>
    <li>
      <Link
        to={url}
        title="Permanentlink"
      >
        <LinkIcon />
      </Link>
    </li>
  </ul>
)

export default ShareLinks


