import React from "react"
import i18n from "linergy-de/i18n"
import { GridCell, GridInner } from "linergy-de/components"
import { withRouter } from "react-static"
import Button from "rmwc/Button"

const CrossedOutLng = () => (
  <div 
    style={{
      width: "120px",
      height: "120px",
      lineHeight: "120px",
      textAlign: "center",
      fontSize: "60px",
      borderRadius: "50%",
      position: "relative"
    }} 
    className="mdc-elevation--z3"
  >
    {i18n.prevLanguage}
    <div style={{
      position: "absolute",
      left: "8px",
      right: "8px",
      height: "4px",
      top: "58px",
      backgroundColor: "rgba(255,0,0,.4)",
      borderRadius: "2px",
      transform: "rotate(-45deg)"
    }} />
  </div>
)

const texts = {
  en: {
    title: "This page is not available in your language",
    body: `Unfortunately the requested page is not available in your chosen 
    language. You can either continue reading this page in German or return 
    to the previous page.`,
    continueReading: "Continue reading",
    goBack: "Go back"
  },
  de: {
    title: "Die Seite ist in Ihrer Sprache nicht verfügbar",
    body: `Leider ist diese Seite nicht in Ihrer Ausgangssprache verfügbar.
    Sie können entweder weiter diese Seite besuchen oder zur vorherigen
    Seite zurückkehren.`,
    continueReading: "Weiterlesen",
    goBack: "Zurück gehen"
  }
}

class NotAvaibleInLng extends React.Component {
  render() {
    let { availableIn } = this.props
    const { history } = this.props 
    availableIn = availableIn || []
    const prevLanguage = i18n.prevLanguage || "de"
    const language = i18n.language || "de"
    if (availableIn.indexOf(language) !== -1) {
      return null
    }
    const { title, body, goBack, continueReading } = texts[prevLanguage] || {}
    return (
      <GridCell 
        span="12"
        id="not-available-in-lng"
        className="mdc-elevation--z1"
        style={{
          padding: "12px",
          backgroundColor: "rgba(255,0,0,.1)"
        }}
      >
        <GridInner>
          <GridCell desktop="3" tablet="2" style={{
            display: "flex",
            justifyContent: "center",
            paddingTop: "0.6em"
          }}>
            <CrossedOutLng />
          </GridCell>
          <GridCell desktop="9" tablet="6">
            <h2>{title}</h2>
            <p>{body}</p>
            <Button
              raised
              theme="secondary-bg text-primary-on-secondary"
              style={{ marginRight: "12px" }}
              onClick={() => {
                i18n.prevLanguage = i18n.language
                this.forceUpdate()
              }}
            >
              {continueReading}
            </Button>
            <Button onClick={() => {
              history.goBack()
            }}>
              {goBack}
            </Button>
          </GridCell>
        </GridInner>
      </GridCell>
    )
  }
}


export default withRouter(NotAvaibleInLng)


